.footer {
  width: 100%;
}

.footer .footer-container {
  width: calc(100% - 60px);
  margin: 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  box-sizing: border-box;
}

.footer .line {
  width: 100%;
  height: 2px;
  background-color: #818181;
}

.footer .line:first-child {
  margin-bottom: 30px;
}

.footer .line:last-child {
  margin-top: 30px;
}

.footer .footer-container > .container {
  width: 100%;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.footer .footer-container > .container .logo,
.footer .footer-container > .container .legal,
.footer .footer-container > .container .about,
.footer .footer-container > .container .share {
  width: 100%;
}

.footer .footer-container > .container .title {
  display: flex;
  justify-content: flex-start;
  margin: 30px 0 10px 0;
}

.footer .footer-container > .container .title .title-wrapper h1 {
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: bold;
  text-align: left;
  margin: 0;
}

.footer .footer-container > .container .title-wrapper .underline {
  width: 100%;
  height: 2px;
  background-color: #cc9966;
  margin-top: 7px;
}

.footer .footer-container > .container .logo {
  width: 100%;
}

.footer .footer-container > .container .logo .container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer .footer-container > .container .logo .logo-img {
  width: 75px;
}

.footer .footer-container > .container .legal,
.footer .footer-container > .container .about,
.footer .footer-container > .container .share {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
}

.footer .footer-container > .container .legal > .container,
.footer .footer-container > .container .about > .container,
.footer .footer-container > .container .share > .container,
.footer .footer-container > .container .legal > .container .title,
.footer .footer-container > .container .about > .container .title,
.footer .footer-container > .container .share > .container .title,
.footer
  .footer-container
  > .container
  .legal
  > .container
  .title
  .title-wrapper,
.footer
  .footer-container
  > .container
  .about
  > .container
  .title
  .title-wrapper,
.footer
  .footer-container
  > .container
  .share
  > .container
  .title
  .title-wrapper {
  display: inline-block;
}

.footer .footer-container > .container .sub-container ul {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer .footer-container > .container .sub-container ul li {
  width: 100%;
}

.footer .footer-container > .container .sub-container ul li a {
  text-decoration: none;
  font-family: "Roboto";
  font-size: 12px;
  color: #000;
}

.footer .footer-container > .container .sub-container ul li a:visited {
  text-decoration: none;
  color: #000;
}

.footer .footer-container > .container .sub-container ul li a:hover {
  font-weight: bold;
}

.footer .footer-container > .container .share .sub-container ul {
  display: flex;
}

.footer .footer-container > .container .share .sub-container ul li {
  width: initial;
  margin-right: 10px;
}

.footer .footer-container > .container .copyright {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
}

.footer .footer-container > .container .copyright p {
  width: 100%;
  text-align: center;
  margin: 0;
  font-size: 10px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #333333;
}

@media screen and (orientation: landscape) {
  .footer .footer-container > .container {
    flex-wrap: wrap;
    flex-direction: row;
    align-items: baseline;
  }

  .footer .footer-container > .container .legal,
  .footer .footer-container > .container .about,
  .footer .footer-container > .container .share {
    width: 50%;
    justify-content: center;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .footer .footer-container > .container .legal,
  .footer .footer-container > .container .about,
  .footer .footer-container > .container .share {
    width: 33%;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .footer .footer-container > .container .legal,
  .footer .footer-container > .container .about,
  .footer .footer-container > .container .share {
    width: 33%;
    justify-content: center;
  }

  .footer .footer-container > .container {
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: row;
    align-items: baseline;
  }

  .footer .footer-container > .container .title .title-wrapper h1 {
    font-size: 18px;
  }

  .footer .footer-container > .container .sub-container ul li a {
    font-size: 16px;
  }

  .footer .footer-container > .container .copyright p {
    font-size: 12px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .footer .footer-container > .container .legal,
  .footer .footer-container > .container .about,
  .footer .footer-container > .container .share,
  .footer .footer-container > .container .logo {
    width: 25%;
    padding: 0 10px;
  }

  .footer .footer-container > .container .logo {
    align-self: center;
  }

  .footer .footer-container {
    max-width: 1024px;
    margin: 0 auto;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}
