.button {
  width: 180px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #191970;
  margin: 0 auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  outline-color: transparent;
}

.button:hover {
  box-shadow: 0px 0px 12px -3px #0057ab;
}

.button:active {
  transform: scale(0.98);
}
