.contact {
  min-width: 300px;
  width: 35%;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  padding: 20px;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin: 30px auto 30px auto;
}

.contact .text-area textarea {
  min-height: 100px;
  height: 200px;
}
