.pin {
  width: 20px;
  height: 30px;
  position: relative;
  cursor: pointer;
}

.pin > button {
  width: 100%;
  height: 100%;
  position: absolute;
  top: -30px;
  right: 10px;
  background-color: transparent;
  border: none;
  padding: 0;
  outline: none;
  cursor: pointer;
}

.pin > button:hover {
  transform: scale(1.2);
}

.pin > button > img {
  width: 100%;
  height: 100%;
}

.pin .card {
  position: absolute;
  top: -330px;
  right: -230px;
  left: initial;
  bottom: initial;
}

.pin .card.right,
.pin .card.left {
  margin-left: auto;
margin-right: auto;
left: 0;
right: 0;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .pin {
    position: relative;
  }
  
  .pin .card.right {
    right: -230px;
    left: initial;
    bottom: initial;
  }
  
  .pin .card.left {
    right: 20px;
    left: initial;
    bottom: initial;
  }

  .pin .card.bot {
    top: 0;
  }
  
  .pin .card.top {
    top: -330px;
  }
}

