.map {
  width: calc(100% - 20px);
  padding: 0;
  margin: 0 10px;
  position: relative;
}

/* Make height in portrait fill and landscape set a min height */
.map .map-wrapper {
  width: 100%;
  height: calc(100vh - 75px);
  margin: 15px 0 15px 0;
  border-radius: 4px;
  overflow: hidden;
  min-height: 320px;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .map .map-wrapper {
    height: calc(100vh - 20px);
    margin: 0;
  }
}

