.suggest-modif {
  position: absolute;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
}

.suggest-modif .relative {
  position: relative;
  height: 100%;
  width: 100%;
}

.suggest-modif .sticky {
  position: sticky;
  height: 100vh;
  width: 100%;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.suggest-modif .container {
  width: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  background-color: #fff;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  padding: 15px;
}

.suggest-modif .container .exit {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.suggest-modif .container .exit img {
  width: 20px;
  height: 20px;
}

.suggest-modif .container > h1 {
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
}

.suggest-modif .container p,
.suggest-modif .container .text-area label {
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
}
