.upload {
  width: 100%;
  margin: 10px 0 20px 0;
  display: flex;
  flex-wrap: wrap;
}

.upload p {
  display: inline-block;
  margin: 0;
}

.upload label {
  width: 70px;
  height: 20px;
  border-radius: 10px;
  background-color: #cc9966;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0 0 0 15px;
}

.upload .content {
  width: 100%;
}
