.suggest {
  width: calc(100% - 20px);
  margin: 40px 10px 0 10px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 14px;
  color: #333333;
}

.suggest .left {
  width: 100%;
  order: 2;
  margin-top: 30px;
}

.suggest .left .map-wrapper {
  border-radius: 4px;
  overflow: hidden;
  height: calc(100vh - 20px);
  width: 100%;
}

.suggest .right {
  width: 100%;
  order: 1;
  position: relative;
}

.suggest .right .suggest-form {
  display: flex;
  flex-direction: column;
  min-height: 600px;
  padding: 20px;
  border-radius: 4px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
}

.suggest .right .fetching {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.suggest .right .login-btns {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 10;
}

.suggest .modal-container {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 1;
}

.suggest .modal-container.hide {
  display: none;
}

.suggest .modal-container.show {
  display: initial;
}

.suggest .right .suggest-form .text-area.description-suggestion {
  flex: 2;
}

.suggest .right .suggest-form .text-area.description-suggestion textarea {
  min-height: 150px;
}

.suggest .right .suggest-form .text-area.justify-suggestion {
  flex: 1;
}

/* pointer for google map */
.gm-style:first-of-type > div:nth-child(1) {
  cursor: pointer !important;
}

/* Address suggestion */
.suggest .right .suggest-form .input-address {
  position: relative;
}

.suggest .right .suggest-form .autocomplete-dropdown-container {
  
}

.suggest .right .suggest-form .dropdown {
  position: absolute;
  top: 55px;
  width: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  z-index: 5;
  border: 1px solid #c1c1c1;
  border-radius: 4px;
  overflow: hidden;
}

.suggest .right .suggest-form .dropdown.show {
  display: initial;
}

.suggest .right .suggest-form .dropdown.hide {
  display: none;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .suggest {
    flex-wrap: nowrap;
    margin: 0 10px;
  }

  .suggest .right {
    width: 30%;
    min-width: 300px;
  }

  .suggest .left {
    order: initial;
    margin: 0 10px 0 0;
  }

  .suggest .left .map-wrapper {
    height: 100%;
  }
}

