.search-bar {
  width: 100%;
  height: 30px;
  margin-top: 25px;
  background-color: #fff;
  border-radius: 4px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
}

.search-bar .search-form {
  height: 100%;
  width: 100%;
  display: flex;
}

.search-bar .search-form .wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5px;
}

.search-bar .search-form .wrapper .search-icon {
  width: 20px;
}

.search-bar .search-form .search-input {
  flex: 1;
  height: 100%;
  width: 100%;
  border: none;
  padding: 0 5px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #000;
  font-size: 16px;
  text-overflow: ellipsis;
  outline: none;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.search-bar .search-form .search-input .input-address {
  width: 100%;
}

.search-bar .search-form .input-address {
  width: 100%;
  position: relative;
}

.search-bar .search-form .input-address .dropdown {
  z-index: 10;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
}

.search-bar .search-form .input-address .autocomplete-dropdown-container {
  border: 1px solid #c1c1c1;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  border: none;
  outline: transparent;
  background-color: #fff;
  overflow: hidden;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.35);
}

.search-bar .search-form .search-input::placeholder {
  color: #9a9a9a;
}
/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .search-bar {
    margin: 0 20px;
    position: absolute;
    top: 20px;
    z-index: 1;
    height: 50px;
    width: initial;
  }

  .search-bar .search-form {
    width: initial;
  }

  .search-bar .search-form .wrapper {
    padding: 0 15px;
  }

  .search-bar .search-form .search-input {
    width: 300px;
    transition: width 0.5s ease;
  }

  .search-bar .search-form .search-input:focus {
    width: 500px;
    transition: width 0.5s ease;
  }
}

