.card {
  width: 250px;
  height: auto;
  border-radius: 4px;
  background-color: #fff;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  cursor: default;
  z-index: 1;
}

.card .card-header {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #32cd32;
}

.card .card-header.green {
  background-color: #32cd32;
}

.card .card-header.orange {
  background-color: #ff8c00;
}

.card .card-header.red {
  background-color: #8b0000;
}

.card .card-header h1 {
  color: #fff;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.card .card-header .card-exit {
  position: absolute;
  top: 5px;
  right: 5px;
}

.card .card-header .card-exit button {
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  padding: 0;
}

.card .card-header .card-exit img {
  width: 20px;
  height: 20px;
}

.card .card-body {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card .card-body h1 {
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: #000;
  font-weight: bold;
  width: 100%;
  text-align: center;
  padding: 0 15px;
}

.card .card-body .description {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  height: 90px;
  overflow: scroll;
  overflow-x: hidden;
  padding: 0 15px;
}

/* width */
.card .card-body .description::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.card .card-body .description::-webkit-scrollbar-track {
  background: transparent; 
}
 
/* Handle */
.card .card-body .description::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
.card .card-body .description::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.card .card-body .date {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 10px;
  padding: 0 15px;
}

.card .card-connect {
  width: 100%;
}

.card .card-connect p {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.card .card-connect .wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card .card-connect .wrapper a img {
  width: 35px;
  height: 35px;
  margin: 0 5px;
}

.card .card-action {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 10px 0;
}

.card .card-action .confirm-btn,
.card .card-action .suggest-btn {
  width: 100%;
  margin: 0 10px;
}

.card .card-action .confirm-btn {
  margin: 0 0 0 10px;
}

.card .card-action .confirm-btn.hide {
  display: none;
}

.card .card-action > div > button {
  width: 100%;
  font-size: 10px;
  text-transform: none;
}

.card .card-history {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.card .card-history > button {
  width: 100%;
  color: #5e5e9b;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 10px;
  text-align: center;
  text-decoration: none;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}
