.history {
  position: absolute;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
}

.history .relative {
  position: relative;
  height: 100%;
  width: 100%;
}

.history .sticky {
  position: sticky;
  height: 100vh;
  width: 100%;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.history .container {
  width: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.35);
  background-color: #fff;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  padding: 15px;
}

.history .container .exit {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.history .container .exit img {
  width: 20px;
  height: 20px;
}

.history .container > h1 {
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
}

.history .container .phase {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.history .container .phase p {
  margin: 0;
  padding: 0;
  border-radius: 20px;
  background-color: #8b0000;
  color: #fff;
  padding: 5px 20px;
}

.history .container .phase.green p {
  background-color: #32cd32;
}

.history .container .phase.orange p {
  background-color: #ff8c00;
}

.history .container .phase.red p {
  background-color: #8b0000;
}

.history .container .last {
  width: 100%;
}

.history .container .last .author {
  font-size: 14px;
  font-weight: bold;
}

.history .container .last .description {
  font-size: 12px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  padding: 0 10px;
}

.history .container .confirm,
.history .container .old {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: transparent;
  border: none;
  outline: transparent;
  font-size: 14px;
  font-weight: bold;
  margin: 0;
  padding: 0;
  white-space: nowrap;
  cursor: pointer;
}

.history .container .confirm .rotate,
.history .container .old .rotate {
  transform: rotate(180deg);
}

.history .container .confirm img,
.history .container .old img {
  width: 15px;
  margin: 0 5px 0 0;
}

.history .container .confirm-drop-down.hide,
.history .container .old-drop-down.hide {
  display: none;
}

.history .container .confirm-drop-down.show,
.history .container .old-drop-down.show {
  display: initial;
}

.history .container .confirm-drop-down ul,
.history .container .old-drop-down ul {
  margin: 0;
  padding: 5px 0 10px 0;
  list-style-type: none;
}

.history .container .confirm-drop-down li,
.history .container .old-drop-down li {
  padding: 0 0 0 20px;
  font-size: 12px;
}

.history .container .old-drop-down li .author {
  text-decoration: underline;
  margin: 0 0 5px 0;
}

.history .container .old-drop-down li .description {
  margin: 0;
}
