.modal {
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #191970;
  padding: 0 10px;
  color: #fff;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: bold;
  border-radius: 4px;
  vertical-align: middle;
}

.modal.success {
  background-color: #191970;
}

.modal.error {
  background-color: #8b0000;
}

.modal p {
  margin: 0;
}

.modal button {
  display: flex;
  background-color: transparent;
  border: none;
  outline-color: transparent;
  cursor: pointer;
  padding: 0;
  z-index: 1;
}

.modal img {
  width: 18px;
  height: 18px;
  z-index: -1;
}
