@import url("../../assets/animations/fadein.css");

.nav {
  position: relative;
  height: 50px;
  width: 100%;
  background-color: #fff;
}

.nav .container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  -webkit-box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.05);
}

.nav .container > .link {
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.nav .container > .link .logo {
  width: 40px;
}

.nav .burger {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.nav .burger .line {
  width: 100%;
  height: 4px;
  border-radius: 10px;
  background-color: #000;
}

.nav .burger .line:nth-child(2) {
  margin: 5px 0;
}

.nav .nav-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #fff;
  -webkit-box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.05);
}

.nav .nav-menu.hide {
  display: none;
}

.nav .nav-menu.show {
  -webkit-animation: fadein 0.3s; /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 0.3s; /* Firefox < 16 */
  -ms-animation: fadein 0.3s; /* Internet Explorer */
  -o-animation: fadein 0.3s; /* Opera < 12.1 */
  animation: fadein 0.3s;
}

.nav .nav-menu .nav-ul {
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 10px 0;
}

.nav .nav-menu .nav-ul .nav-li {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.nav .nav-menu .nav-ul .nav-li .link {
  padding: 2px 10px;
  text-decoration: none;
  cursor: pointer;
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: bold;
  color: #000;
}

.nav .nav-menu .nav-ul .nav-li .link:hover {
  text-decoration: none;
}

.nav .nav-menu .nav-ul .nav-li .link:visited {
  color: #000;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .nav {
    order: 3;
    background-color: transparent;
    height: 70px;
  }

  .nav .nav-menu {
    background-color: transparent;
  }

  .nav .container {
    display: none;
  }

  .nav .nav-menu.hide,
  .nav .nav-menu.show {
    display: flex;
  }

  .nav .nav-menu {
    position: initial;
    width: 100%;
    height: 100%;
    box-shadow: none;
  }

  .nav .nav-menu .nav-ul {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .nav .nav-menu .nav-ul .nav-li {
    width: auto;
  }

  .nav .nav-menu .nav-ul .nav-li .link {
    font-size: 18px;
  }
}

